@mixin font-myfont($family) {
    @import url('');
}


@font-face {
    font-family: 'TheJamsil5Bold';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/TheJamsil5Bold.woff2') format('woff2');
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'SB Agro B';
    src: url("../Assets/fonts/SB_Agro_B.ttf") format("truetype");
    font-weight: 500;
}

@font-face {
    font-family: 'YangJin';
    src: url("../Assets/fonts/yangjin.ttf") format("truetype");
    font-weight: 500;
}

@font-face {
    font-family: 'Pretendard-Regular';
    src: url('../Assets/fonts/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'baskin';
    src: url('../Assets/fonts/배스킨라빈스 B.ttf') format('truetype');
    font-weight: 700;
    font-style: normal;
}

@include font-myfont('SB Agro B');
@include font-myfont('YangJin');
@include font-myfont('Pretendard-Regular');
@include font-myfont('TheJamsil5Bold');
@include font-myfont('baskin');
