@keyframes filling {
  from{
    background-position: center 0vw;
  }
  to {
    background-position: center 47vw; // 47
  }
}
  
.container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  overflow: hidden;
  background-size: cover;
  background-position: center 1%;
}

.portrait {
  position: relative;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  top: -6vh;
}

.filling_text {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;  
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  color: #FFFFFF;
  // padding-top: 20px;
  margin-bottom: 10%;
  font-size: 10vw;
  animation: filling 3s ease forwards;
  text-align: center; 
  line-height: 1.1;
  -webkit-text-stroke: 3px rgb(0, 0, 0);
}

.guideText {
  position: absolute;
  width: 100%;
  height: 5vh;
  text-align: center;
  bottom: 1vh;
  font-size: 1vw;
  // color: rgba($color: rgb(82, 99, 108), $alpha: 0.4);
  color: rgba($color: rgb(97, 99, 100), $alpha: 0.4);
  font-family: 'Pretendard-Regular';
  font-weight: 500;
}


.App {
  height: 100vh;
  width: 100%;
  display: flex;
  box-sizing: border-box;
  flex-flow: column;
  overflow-x: hidden;

  main {
    display: flex;
    overflow: hidden;
    height: calc(100%);
    margin-top: 0rem;

    & > div {
      height: 100%;
      overflow-x: hidden;
      display: flex;
      align-items: center;
      flex-flow: row;
    }
  }
}

