.mainPageContainer {
  width: 100%;
  height: 100%;
  display: block;
  flex-flow: row;
  box-sizing: border-box;
  position: relative;
  font-size: 0.85rem;
  overflow: hidden;
  overflow-y: hidden;
}

.backBTN {
  position: fixed;
  color: white;
  top: 0;
  font-size: 2rem;
  padding-left: 1vw;
  padding-top: 1vh;
  font-family: 'Do Hyeon', sans-serif;
  font-style: italic;
  font-weight: bold;
  z-index: 201;
}

.backBTN:hover {
  cursor: pointer;
}

.sound {
  position: fixed;
  color: white;
  width: 3vw;
  height: 3vw;
  right: 1vw;
  z-index: 201;
  background-size: cover;
}

.sound:hover {
  cursor: pointer;
}

