.container {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    font-family: 'SBAggroB';
    font-family: 'VITRO INSPIRE TTF';

    img {
        width: 100%;
        height: auto;
        object-fit: cover;
    }

    .movingLetters {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        margin: 0;
        font-size: 5rem;
        font-weight: bold;
        text-transform: uppercase;
        color: black;
        text-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
        overflow: hidden;

        span {
            display: block;
            text-align: center;
        }

        .letters {
            display: inline-block;
            position: absolute;
        }

        .letters1 {
            margin-right: -0.8rem;
            opacity: 0;
        }

        .letters2 {
            margin-left: -0.8rem;
            opacity: 0;
        }
    }

    .letters.scale-in {
        transform: scale(1);
    }

    .letters.scale-out {
        transform: scale(3);
        opacity: 0;
    }

    .ring {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 150px;
        height: 150px;
        background: transparent;
        border: 3px solid #3c3c3c;
        border-radius: 50%;
        text-align: center;
        line-height: 150px;
        font-family: sans-serif;
        font-size: 20px;
        color: #ff1e00;
        letter-spacing: 4px;
        text-transform: uppercase;
        text-shadow: 0 0 10px #ff1e00;
        box-shadow: 0 0 20px rgba(0, 0, 0, .5);
      }
      
      .ring:before {
        content: '';
        position: absolute;
        top: -3px;
        left: -3px;
        width: 100%;
        height: 100%;
        border: 3px solid transparent;
        border-top: 3px solid #ff1e00;
        border-right: 3px solid #ff1e00;
        border-radius: 50%;
        animation: animateC 2s linear infinite;
      }
      
      .ringDot {
        display: block;
        position: absolute;
        top: calc(50% - 2px);
        left: 50%;
        width: 50%;
        height: 4px;
        background: transparent;
        transform-origin: left;
        animation: animate 2s linear infinite;
      }
      
      .ringDot:before {
        content: '';
        position: absolute;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background: #ff1e00;
        top: -6px;
        right: -8px;
        box-shadow: 0 0 20px #ff1e00;
      }
      
      @keyframes animateC {
        0% {
          transform: rotate(0deg);
        }
      
        100% {
          transform: rotate(360deg);
        }
      }
      
      @keyframes animate {
        0% {
          transform: rotate(45deg);
        }
      
        100% {
          transform: rotate(405deg);
        }
      }
}